import { create } from 'zustand'

export const useAppWindowRender = create(set => ({
  //  : get  
  zIndexTemp: 1, //       zindex     
  windowRenderObj: {
    myInfoWindow: {
      zIndexValue: 3, //   zIndex   
      isOpen: true, //   
    },
    myProjectWindow: {
      zIndexValue: 2,
      isOpen: true,
    },
    trashCanWindow: {
      zIndexValue: 2,
      isOpen: false,
    },
    guestBookWindow: {
      zIndexValue: 2,
      isOpen: false,
    },
    blogWindow: {
      zIndexValue: 2,
      isOpen: false,
    },
    alertWindow: {
      zIndexValue: 2, //   zIndex   
      isOpen: false, //   
    },
    loginWindow: {
      zIndexValue: 2, //   zIndex   
      isOpen: false, //   
    },
    helpWindow: { // infoWindow  myInfoWindow  ..
      zIndexValue: 2, //   zIndex   
      isOpen: true, //   
    },
  },
  onClickWindow: name => {
    set(state => {
      const newState = {
        ...state,
        zIndexTemp: state.zIndexTemp + 1, // zIndexTemp 
        windowRenderObj: {
          ...state.windowRenderObj,
          [name]: { ...state.windowRenderObj[name], zIndexValue: state.zIndexTemp },
        },
      }
      return newState
    })
  },
  onClickWindowClose: name => {
    set(state => ({
      windowRenderObj: {
        ...state.windowRenderObj,
        [name]: { ...state.windowRenderObj[name], isOpen: false },
      },
    }))
  },
  onClickWindowOpen: name => {
    set(state => {
      const newState = {
        ...state,
        zIndexTemp: state.zIndexTemp + 1, // zIndexTemp 
        windowRenderObj: {
          ...state.windowRenderObj,
          [name]: { ...state.windowRenderObj[name], isOpen: true, zIndexValue: state.zIndexTemp },
        },
      }
      return newState
    })
  },
  //    
  closeAllWindow: () => {
    set(state => ({
      // 2.    (fomrEntries       )
      windowRenderObj: Object.fromEntries(
        // 1.   (entries    2  )
        Object.entries(state.windowRenderObj).map(([key, value]) => [key, { ...value, isOpen: false }]),
      ),
    }))
  },
}))
